.header {
    max-width: 100%;
    margin: 3.52vw 0 0;
    padding-bottom: calc(50px - (1280px - 100vw) / 102.1);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    justify-items: center;
    align-items: center;
    z-index: 3;
}

.header__navbar {
    background-color: #d5d4d4;
    border-radius: 9px;
    display: flex;
    margin-left: 5vw;
}

.header__logo {
    width: 250px;
    height: 34px;
    margin: 0 0 0 10px;
}

@media (max-width: 1000px) {

    .header__logo {
        width: 205px;
        height: 28px;
        margin: 0;
    }
}

@media (max-width: 880px) {

    .header {
        flex-direction: column;
        padding-bottom: 20px;
    }

    .header__navbar {
        flex-wrap: wrap;
        justify-content: center;
        margin: 10px 0 0;
    }
}

@media (max-width: 500px) {

    .header__navbar {
        flex-wrap: wrap;
        justify-content: center;
        margin: 10px 0 0;
    }
}