.background {
    background-color: #d5d4d4;
}

.page {
    max-width: 1280px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0 auto;
    font-family: 'Myriad Pro', sans-serif;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    word-wrap: break-word;
}

.section {
    flex-grow: 1;
}

.column {
    display: flex;
    flex-direction: column;
}

.text {
    text-align: center;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: normal;
    line-height: 1.5;
    z-index: 5;
}

.text_italic {
    font-style: italic;
    padding-right: 20px;
    text-transform: uppercase;
    font-size: 15px;
    text-align: justify;
    margin: 0 0 5px 0;
    padding-left: 0px;
    font-weight: 200;
    opacity: .7;
}

.title {
    display: none;
}

.flex {
    display: flex;
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.grid {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, 390px);
    grid-gap: 60px 40px;
    justify-content: center;
}


@media (max-width: 1300px) {

    .page {
        max-width: 90vw;
    }
}

@media (max-width: 1100px) {

    .flex {
        align-items: center;
        flex-direction: column;
    }

    .text_italic {
        font-size: 12px;
    }
}

@media (max-width: 950px) {

    .grid {
        grid-template-columns: repeat(auto-fit, 390px);
        gap: 30px;
    }
}

@media (max-width: 850px) {

    .text {
        font-size: 12px;
    }
}

@media (max-width: 550px) {

    .text_italic {
        font-size: 11px;
        width: 90%;
    }
}


@media (max-width: 450px) {

    .grid {
        grid-template-columns: repeat(auto-fit, 290px);
        grid-gap: 20px;
    }

}