.card {
    background-color: #f1f0f0;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 160px auto;
    background-image: url('../../images/cards/default-background.png');
    width: 390px;
    height: 430px;
    border-radius: 30px;
    z-index: 5;
}

.card__relative-container {
    position: relative;
}

/* МОДИФИКАТОРЫ КАРТОЧКИ ПО ТИПУ */

.card_type_with-text {
    z-index: 7;
    height: 505px;
    width: 60vw;
    max-width: 600px;
    border-radius: 30px;
    overflow: hidden;
    padding: 5px 0 25px 20px;
    background-color: #f1f0f0;
    background-image: none;
    height: 520px;
}

.card_type_with-menu {
    height: 550px;
    border-radius: 30px;
    display: -ms-grid;
    display: grid;
    grid-template-areas: 'kit kit kit'
        '. title pic'
        '. article pic'
        '. price pic'
        'navbar navbar navbar';
    -ms-grid-columns: 40px auto 40px;
    grid-template-columns: 40px auto 40px;
    -ms-grid-rows: 240px auto;
    grid-template-rows: 240px auto;
    justify-items: start;
    z-index: 5;
    overflow: hidden;
    height: 550px;
}

.card_type_with-btn {
    display: -ms-grid;
    display: grid;
    grid-template-areas: 'kit kit kit'
        '. title title'
        '. article btn'
        '. price btn';
    column-gap: 38px;
    -ms-grid-columns: 0 1.8fr 2fr;
    grid-template-columns: 0 1.8fr 2fr;
    -ms-grid-rows: 240px auto;
    grid-template-rows: 240px 70px auto;
    justify-items: start;
    -webkit-transition: -webkit-transform .3s ease-in-out;
    transition: -webkit-transform .3s ease-in-out;
    -o-transition: transform .3s ease-in-out;
    transition: transform .3s ease-in-out;
    transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}

.card_type_with-btn:hover {
    -webkit-transform: scale(1.01, 1.01);
    -ms-transform: scale(1.01, 1.01);
    transform: scale(1.01, 1.01);
}

