/* ССЫЛКА-КНОПКА КАРТОЧКИ */

.card__link-btn {
    background-image: url('../../images/cards/default-btn.png');
    background-position: center top;
    background-repeat: no-repeat;
    background-size: contain;
    border: none;
    width: 130px;
    height: 90px;
    justify-self: start;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    cursor: pointer;
    font-size: 13.5px;
    text-align: center;
    margin: 0;
    padding: 0 0 14px 0;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    opacity: 1;
    grid-area: btn;
    box-sizing: border-box;
}

.card__link-btn_status_inactive {
    color: #E9E9E9;
    position: relative;
    opacity: .7;
    transition: opacity .3s ease-in-out;
}

.card__link-btn_status_inactive:hover {
    position: relative;
    opacity: 1;
}

.card__link-btn_status_inactive::after {
    content: '';
    background-image: url('../../images/cards/default-btn-inactive.png');
    background-position: center 15px;
    width: 130px;
    height: 90px;
    margin: -5px 0 0 0;
    padding: 0 0 15px 0;
    position: absolute;
}

.card__link-btn_status_active {
    opacity: 1;
    color: #fff;
}

.card__link-btn_status_active::after {
    display: none;
}

.card__link-btn_rotate {
    background-position: center bottom;
    margin: 0 -35px 0;
    padding: 0;
    transform: rotate(180deg);
}

.card__link-btn_level_machines {
    background-image: url('../../images/cards/machines-btn.png');
}

.card__link-btn_level_machines-m {
    background-image: url('../../images/cards/machines-btn.png');
