.card__link-btn_level_aero-prog {
    background-image: url('../../images/cards/aero-btn.png');
}

.card__link-btn_more {
    transition: background-image .3s ease-in-out;
    margin-top: 10px;
}

.card__link-btn_more:hover {
    background-image: url('../../images/cards/default-btn.png');
}

/* КНОПКА НАЗАД КАРТОЧКИ */

.card__link-back {
    z-index: 6;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    opacity: 1;
    width: 110px;
    height: 90px;
    position: absolute;
    top: 35px;
    left: 0;
}

.card__link-back:hover {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}

.card__back-btn {
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
    height: 90px;
    width: 100px;
    overflow: hidden;
    border: none;
    padding: 0;
    position: absolute;
    top: 0;
    background-image: url('../../images/cards/default-btn.png');
    background-position: right bottom;
    background-repeat: no-repeat;
    background-size: 140px 88px;
    box-shadow: 0 6px 4px -4px rgba(0, 0, 0, .2);
}

.card__back-btn-text {
    text-align: left;
    font-size: 16px;
    text-transform: uppercase;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: end;
    opacity: 1;
    color: #fff;
    margin: 0;
    padding: 0 0 14px 12px;
    box-sizing: border-box;
}

.card__mini-back-btn {
    background-color: rgba(255, 255, 255, 0);
    background-image: url('../../images/cards/back-btn.png');
    background-position: bottom left;
    background-repeat: no-repeat;
    background-size: 40px auto;
    border: none;
    height: 90px;
    width: 150px;
    justify-self: end;
    cursor: pointer;
    position: relative;
    left: -48px;
    top: 4px;
    z-index: 6;
    transition: transform .3s ease-in-out;
    padding: 0;
}

.card__mini-back-btn:hover {
    transform: scale(1.05, 1.05);
}

/* ФОНОВАЯ КАРТИНКА КАРТОЧКИ */

.card__pic {
    grid-area: pic;
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
    width: 180px;
    -ms-grid-column-align: start;
    justify-self: start;
    margin: -25px 0 0 -40px;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

