/* ДРУГИЕ ЭЛЕМЕНТЫ КАРТОЧКИ */

.card__article {
    grid-area: article;
    font-weight: bold;
    font-size: 15px;
    margin: 0;
    align-self: center;
    color: #3d3d3d;
}

.card__text {
    height: 100%;
    width: 100%;
    border-radius: 0;
    overflow-y: scroll;
    list-style: none;
    padding: 0;
    border-bottom-style: solid;
    border-bottom-width: 0;
    display: grid;
    grid-template-columns: auto;
    gap: 40px;
    font-size: 16px;
    text-align: justify;
    text-transform: none;
}

.card__rotate-text {
    transform: rotate(180deg);
    margin: 0;
    padding-top: 8px;
}

.card__li {
    border-bottom-style: solid;
    border-bottom-width: 3px;
}

.card__li:last-of-type {
    border-bottom-width: 0;
}

.card__title {
    grid-area: title;
    font-weight: bold;
    text-align: left;
    font-size: 15px;
    margin: 0;
    color: #3d3d3d;
    max-width: 90%;
}

.card__title_big {
    font-size: 17px;
    text-align: justify;
    padding-right: 10px;
    padding-bottom: 10px;
}

.card__price {
    grid-area: price;
    font-size: 15px;
    margin: 0;
    font-weight: bold;
}

.card__navbar {
    -ms-grid-column-align: center;
    justify-self: center;
    grid-area: navbar;
    display: flex;
    padding: 0 35px 10px 35px;
}

.card__list {
    margin: 0 0 20px 0;
    padding: 0 20px;
    list-style: disc;
    color: #3d3d3d;
    display: grid;
    grid-template-columns: repeat(1, auto);
    column-gap: 70px;
}

.card__list-item {
    text-align: justify;
    margin: 0 0 5px 0;
    padding-left: 0px;
    text-transform: none;
    font-size: 18px;
    font-weight: 400;
}

.card__img {
    grid-area: kit;
    height: 220px;
    width: 280px;
    -ms-grid-column-align: end;
    justify-self: end;
    -o-object-fit: contain;
    object-fit: contain;
    margin: 10px;
}

.card__img-top {
    justify-self: left;
    width: 45%;
    margin: -40px 0 0 20px;
}

.card__img-bot {
    justify-self: right;
    width: 45%;
    margin: -100px 40px 0 0;
}

@media (max-width: 1200px) {

    .card_type_with-text {
        width: 40vw;
    }
}

@media (max-width: 1100px) {

    .card_type_with-text {
        width: 370px;
    }

    .card__list {
        display: flex;
        flex-direction: column;
    }

    .card__list-item {
        text-align: justify;
        font-size: 16px;
    }

    .card__title_big {
        font-size: 15px;
    }

    .card__img-bot {
        margin-top: -50px;
    }
}

@media (max-width: 550px) {

    .card__mini-back-btn {
        width: 0;
        background-image: none;
    }
}

@media (max-width: 450px) {

    .card {
        margin-bottom: 10px;
        width: 290px;
        height: 330px;
        background-size: 140px auto;
    }

    .card_type_with-btn {
        -ms-grid-columns: 15px 115px auto;
        grid-template-columns: 0 115px auto;
        -ms-grid-rows: 180px auto;
        grid-template-rows: 180px auto;
        column-gap: 20px;
    }

    .card_type_with-text {
        width: 270px;
        height: 400px;
    }

    .card_type_with-menu {
        height: 400px;
        -ms-grid-columns: 15px auto;
        grid-template-columns: 15px auto;
        -ms-grid-rows: 180px auto;
        grid-template-rows: 180px auto;
        grid-template-areas: 'kit kit'
            '. title'
            '. article'
            '. price'
            'navbar navbar';
    }

    .card__rotate-text {
        padding-top: 3px;
    }

    .card__article {
        font-size: 13px;
    }

    .card__img {
        height: 140px;
        width: 200px;
    }

    .card__price {
        font-size: 12px;
    }

    .card__title {
        font-size: 12px;
        width: 90%
    }

    .card__link-btn {
        width: 110px;
        height: 70px;
        font-size: 11px;
        padding-bottom: 3px;
    }

    .card__list-item {
        font-size: 13px;
        width: 90%;
    }

    .card__pic {
        display: none;
    }

    .card__link-back {
        top: 32px;
        width: 85px;
        height: 76px;
    }

    .card__back-btn {
        width: 85px;
        height: 76px;
        background-size: 120px 75px;
    }

    .card__back-btn-text {
        font-size: 13px;
    }

    .card__link-btn_status_inactive::after {
        background-position: center 0;
        width: 110px;
        height: 70px;
    }
}