.main__navbar {
    margin: 40px 0 0 0;
    padding: 0;
    z-index: 5;
    height: min-content;
    display: grid;
    align-items: end;
    row-gap: 30px;
    grid-template-areas: "dobot preliminary ...          ..."
        "...   elementary  basic        ..."
        "...   ...         professional research"
        "...   ...         ...          aero";
}

.main__link {
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 -50px 0 0;
    padding: 0;
    height: 100px;
    width: 173px;
    font-size: 0;
    transition: background-image .25s linear, transform .25s linear;
}

.main__link:hover {
    transform: scale(1.1, 1.1);
}

/* МОДИФИКАТОРЫ ССЫЛКИ ПО МОДУЛЮ */

.main__link_level_research {
    background-image: url(../../images/link/research-inactive.png);
    grid-area: research;
}

.main__link_level_research:hover {
    background-image: url(../../images/link/research-active.png);
}

.main__link_level_professional {
    background-image: url(../../images/link/professional-inactive.png);
    grid-area: professional;
}

.main__link_level_professional:hover {
    background-image: url(../../images/link/professional-active.png);
}

.main__link_level_preliminary {
    background-image: url(../../images/link/preliminary-inactive.png);
    grid-area: preliminary;
}

.main__link_level_preliminary:hover {
    background-image: url(../../images/link/preliminary-active.png);
}

.main__link_level_elementary {
    background-image: url(../../images/link/elementary-inactive.png);
    grid-area: elementary;
}

.main__link_level_elementary:hover {
    background-image: url(../../images/link/elementary-active.png);
}

.main__link_level_dobot {
    grid-area: dobot;
    background-image: url(../../images/link/dobot-logo-inactive.svg);
}

.main__link_level_dobot:hover {
    background-image: url(../../images/link/dobot-logo.svg);
}

.main__link_level_basic {
    background-image: url(../../images/link/basic-inactive.png);
    grid-area: basic;
}

.main__link_level_basic:hover {
    background-image: url(../../images/link/basic-active.png);
}

.main__link_level_aero {
    background-image: url(../../images/link/aero-inactive.png);
    grid-area: aero;
}

.main__link_level_aero:hover {
    background-image: url(../../images/link/aero-active.png);
}

/* МОДИФИКАТОРЫ АКТИВНОЙ ССЫЛКИ ПО МОДУЛЮ */

.main__active-link_level_research {
    background-image: url(../../images/link/research-active.png);
}

.main__active-link_level_professional {
    background-image: url(../../images/link/professional-active.png);
}

.main__active-link_level_preliminary {
    background-image: url(../../images/link/preliminary-active.png);
}

.main__active-link_level_elementary {
    background-image: url(../../images/link/elementary-active.png);
}

.main__active-link_level_basic {
    background-image: url(../../images/link/basic-active.png);
}

.main__active-link_level_aero {
    background-image: url(../../images/link/aero-active.png);
}

@media (max-width: 1250px) {

    .main__link {
        height: 90px;
        width: 155px;
    }
}

@media (max-width: 1100px) {

    .main__navbar {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 0 30px -50px;
    }

    .main__link {
        margin-right: -50px;
    }
}

@media (max-width: 880px) {

    .main__navbar {
        margin-left: -30px;
    }

    .main__link {
        margin-right: -30px;
        height: 80px;
        width: 120px;
    }

    .main__link:hover {
        opacity: .6;
    }

    .main__link_level_research {
        background-image: url(../../images/link/research-active.png);
    }

    .main__link_level_professional {
        background-image: url(../../images/link/professional-active.png);
    }

    .main__link_level_preliminary {
        background-image: url(../../images/link/preliminary-active.png);
    }

    .main__link_level_elementary {
        background-image: url(../../images/link/elementary-active.png);
    }

    .main__link_level_dobot {
        background-image: url(../../images/link/dobot-logo.svg);
    }

    .main__link_level_basic {
        background-image: url(../../images/link/basic-active.png);
    }

    .main__link_level_aero {
        background-image: url(../../images/link/aero-active.png);
    }
}

@media (max-width: 740px) {

    .main__navbar {
        max-width: 350px;
    }
}